body{
    margin: 0;
    padding: 0;
    font-family: "SF Pro SC", "SF Pro Display", "SF Pro Icons", "PingFang SC", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
    font-size: 24px;
}

.image {
    text-align: center;
    object-fit: cover;
}

aside {
    width: 300px;
}

.center {
    text-align: center;
}

.maindiv {
    display: flex;
    width: 100%;
}

.beaut-txt {
    color: teal;
    padding: 15px;
    margin: 10px;
    font-weight: 500;
}

.leftnav {
    width: 16%;
    background-color: #E9E9EB;
    font-size: 24px;
    color: #42506B;
    min-height: 960px;
    padding: 20px;
}

.leftnav li {
    list-style: square;
    padding-top: 15px;
}

ul li a {
    text-decoration: none;
}

.rightmain {
    width: 84%;
    min-height: 960px;
    font-size: 24px;
    padding: 20px;
}

.topnav {
    height: 50px;
    background-color: #ccc;
}

.topnav ul {
    display: flex;
}

.topnav ul li {
    width: 180px;
    list-style: none;
    text-align: center;
    line-height: 50px;
}

.gridgirl {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(auto-fit, minmax(700px, 1fr));
}

.gridgirl div img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.ant-carousel .slick-slide {
    text-align: center;
    /* width: 1000px; */
    height: 200px;
    line-height: 200px;
    background: #364d79;
    overflow: hidden;
}

.ant-carousel .slick-slide h3 {
    color: #fff;
}

.ci-bgm {
    height: 700px;
    text-align: center;
    line-height: 700px;
    width: 100%;
    filter: blur(40px);
}

.ci-box {
    overflow: hidden;
    display:flex;
    justify-content:center;
    align-items: center;
}

.ci-content {
    font-size: 40px;
    color: deeppink;
    position: absolute;
}
.bracket-left{
    position: absolute;
    left: -80px;
    top:-60px;
}
.bracket-right{
    position: absolute;
    right: -80px;
    top:60px;
}
.rightmain h3{
    font-size: 24px;
    color: teal;
}